<template>
  <div class="header">
    <div class="header-left">
      <Icon type="ios-arrow-back" size="30" />Back
    </div>
    <div class="header-middle">
      <span>{{title}}</span>
    </div>
    <div class="header-right">
      <span @click="logout">Logout</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "HeaderBar",
  props: {
    title: String
  },
  methods: {
    logout: function() {
      this.$fb.auth.signOut().then(() => this.$router.replace("login"));
    }
  }
};
</script>

