<template>
  <div class="login-form">
    <h1 style="margin-bottom: 30px">Simple Timesheet</h1>
    <Form ref="loginForm" @keydown.enter.native="login">
      <FormItem prop="email">
        <Input v-model="email" placeholder="john.due@gmail.com">
          <span slot="prepend">
            <Icon :size="16" type="ios-person"></Icon>
          </span>
        </Input>
      </FormItem>
      <FormItem prop="password">
        <Input type="password" v-model="password" placeholder="verysimple">
          <span slot="prepend">
            <Icon :size="14" type="md-lock"></Icon>
          </span>
        </Input>
      </FormItem>
      <FormItem>
        <Button @click="login" type="primary" long>Login</Button>
      </FormItem>
    </Form>
  </div>
</template>
<script>
export default {
  name: "Login",
  data: function() {
    return {
      email: "",
      password: "",
      showError: false
    };
  },
  methods: {
    login: function() {
      this.$fb.auth
        .signInWithEmailAndPassword(this.email, this.password)
        .then(() => {
          this.$router.replace("/");
        })
        .catch(err => (this.showError = true));
    }
  }
};
</script>
<style scoped>
.login-form {
  margin-top: 50px;
  padding: 16px;
}
</style>
